import { DeepPartial } from '../core'
import { WEB3_DEFAULT_ADDR } from '../web3-client'
import { VaultRequest } from './vault-request.model'
import { VaultBlockRequestMock } from '../vault-blocks'

export function VaultRequestMock(
  options?: DeepPartial<VaultRequest>
): VaultRequest {
  const request = VaultBlockRequestMock(options)

  return {
    ...request,
    vaultId: options?.vaultId || request.vaultId || 'VAULT_ID',
    vaultAddress:
      options?.vaultAddress || request.vaultAddress || WEB3_DEFAULT_ADDR,
  }
}
